import React from "react";

import SettingsIcon from "@material-ui/icons/Settings";

import Page from ".";

export default class SettingsPage implements Page {
  title = "Settings";
  path = "/settings";

  icon = SettingsIcon;

  enabled(device?: DeviceState): bool {
    return device != null && device.services.settings != null;
  }

  page: React.FC = () => {
    return (
      <React.Fragment/>
    );
  }
}
